<script lang="ts">
  import axios from 'axios';
  
  import { FLAG_YT_SEARCH_POPUP, YT_VIDEO_ID } from './stores';

  export let YT_API_KEY: string;

  let ytQuery: string;
  let ytURL: string;``
  const searchYT = async () => {
    items = await axios.get(`https://content-youtube.googleapis.com/youtube/v3/search?part=snippet&q=${ytQuery}&maxResults=50&key=${YT_API_KEY}`).then(res => res.data.items);
		console.log(items);
	};

  const playYT = () => {
    YT_VIDEO_ID.set(ytURL.split('watch?v=')[1]);
    FLAG_YT_SEARCH_POPUP.set(false);
  };

  const htmlEntities = (str: string) => {
    return String(str).replace('&amp;', '&').replace('&lt;', '<').replace('&gt;', '>').replace('&quot;', '"');
  };

	let items: any = null;
</script>

<div id="yt-search-popup">
  <div class="viewport">
    <div class="exit-btn" on:click={() => {FLAG_YT_SEARCH_POPUP.set(false);}}></div>
    <div class="interface link">
      <div class="viewport-title">유튜브 주소로 추가</div>
      <input type="text" placeholder="유튜브 영상 주소를 입력하세요" bind:value={ytURL}>
      <button on:click={playYT}>추가</button>
    </div>
    <div class="interface search">
      <div class="viewport-title">유튜브 검색</div>
      <input type="text" placeholder="유튜브 검색어를 입력하세요" bind:value={ytQuery}>
      <button on:click={searchYT}>검색</button>
    </div>
    <div id="yt-search-results">
      {#if items != null}
        {#each items as item}
          {#if typeof item.snippet.title == 'string' && item.id.kind == 'youtube#video'}
            <div id="yt-search-result">
              <div class="thumb">
                <img src="{item.snippet.thumbnails.medium.url}">
              </div>
              <div class="info">
                <div class="title">{htmlEntities(item.snippet.title)}</div>
                <div class="upload-date">{htmlEntities(item.snippet.publishedAt)} - {htmlEntities(item.snippet.channelTitle)}</div>
                <div class="desc">{htmlEntities(item.snippet.description)}</div>
              </div>
            </div>
          {/if}
        {/each}
      {/if}
    </div>
  </div>
</div>

